@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: #E4E8EB;
}

nav,
footer.page-footer {
  background-color: #040404;
}

main {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
}

stk-header nav {
  height: 104px;
}

stk-header .nav-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

stk-header .brand-logo i {
  font-size: 3em;
}

stk-header .menu ul li {
  margin-right: 10px;
}

stk-header .menu,
stk-header .menu-mobile {
  text-transform: uppercase;
}

stk-header .button-collapse {
  cursor: pointer;
}

stk-menu-button .stk-menu-btn {
  background: transparent;
  outline: none !important;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
}

stk-menu-button .stk-menu-btn-animate {
  width: 100%;
  height: 40px;
  line-height: 42px;
  padding: 0;
  border: none;
}

stk-menu-button .stk-menu-btn-animate span {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  padding: 0 25px;
}

stk-menu-button .stk-menu-btn-animate span:before, stk-menu-button .stk-menu-btn-animate span:after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  background: white;
  transition: all 0.3s ease;
}

stk-menu-button .stk-menu-btn-animate span:before {
  width: 2px;
  height: 0%;
}

stk-menu-button .stk-menu-btn-animate span:after {
  width: 0%;
  height: 2px;
}

stk-menu-button .stk-menu-btn-animate span:hover:before {
  height: 100%;
}

stk-menu-button .stk-menu-btn-animate span:hover:after {
  width: 100%;
}

stk-menu-button .stk-menu-btn-animate:before, stk-menu-button .stk-menu-btn-animate:after {
  position: absolute;
  content: "";
  right: 0;
  top: 0;
  background: white;
  transition: all 0.3s ease;
}

stk-menu-button .stk-menu-btn-animate:before {
  height: 0%;
  width: 2px;
}

stk-menu-button .stk-menu-btn-animate:after {
  width: 0%;
  height: 2px;
}

stk-menu-button .stk-menu-btn-animate:hover:before {
  height: 100%;
}

stk-menu-button .stk-menu-btn-animate:hover:after {
  width: 100%;
}

stk-home h1 {
  text-align: center;
}

stk-home .objective .title {
  font-size: 1.7rem;
  font-weight: 500;
  margin-top: 5px;
  margin-bottom: 0;
}

stk-home .objective i {
  margin: 40px 0;
  color: #950000;
  font-size: 7rem;
  display: block;
}

stk-footer {
  color: white;
}

stk-footer .social-links {
  text-align: right;
}

stk-footer a.social-link {
  padding: 0 10px;
  color: inherit;
  text-decoration: none;
  font-size: 3em;
}

stk-footer a.social-link:hover {
  color: #950000;
}

